m'>查看</a>\n          </td>\n          <td>\n             {{item.time}}\n          </td>\n          <td>\n              <a href=\"javascript:;\" :class=\"[item.remind_status==0?'col-1EA': 'col-0ACD00']\" class='adopt' @click='DeliverGoods(item)'>{{item.remind_status==0?'提醒发货':'已提醒'}}</a>\n          </td>\n        </tr>\n      </table>\n     \n      <el-dialog title=\"收货地址\" :visible.sync=\"regionDialogVisible\" width=\"400px\" center >\n            <div class='reason'>\n                {{`${reasonData.address}${reasonData.details}`}}\n            </div>\n     </el-dialog>\n      <!--分页组件-->\n      <div class=\"page_center\">\n        <paging :page-count=\"pageCount\"   :page=\"page\" @index=\"paging\"></paging>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\n  export default {\n    data(){\n      return{\n        token:window.sessionStorage.getItem(\"bbytoken\"),\n        pageCount:1,  //总页数,默认1\n        page:1,       //当前页\n        dataList:[],\n        regionDialogVisible:false,\n        reasonData:{},\n        name:'',\n        picker:'' \n      }\n    },\n    computed:{\n        pickerTime(){\n            let pickerData=this.picker;\n            return{\n                start_time: pickerData && pickerData[0] ? pickerData[0] :'',\n                end_time: pickerData && pickerData[1] ? pickerData[1] :'',\n            }\n        }\n    },\n    methods:{\n      //初始化数据\n      init(){\n       \n        this.$axios.post('admin/SmocRationMsg/totalShipment',{\n            token:this.token,\n            status:0,\n            page:this.page,\n            search:this.name,\n            start_time:this.pickerTime.start_time,\n            end_time:this.pickerTime.end_time\n        }).then(res=>{\n          if(res.data.code==1){\n          this.dataList=res.data.data.list; //拿返回来的数据\n       \n          this.pageCount=res.data.data.rows  //拿取总页数\n        }else{\n          this.dataList={}\n          layer.msg(res.data.msg,{time:1000}) //如果code不等于一代表,没有数据，提示暂无数据\n        }\n      }).catch(err=>console.log(err))\n      },\n    \n      /**发货 */\n      DeliverGoods(data){ \n            if(data.remind_status==0){\n                this.$axios.post('admin/SmocRationMsg/shippingReminder',{\n                token:this.token,\n                sm_id:data.sm_id,\n                id:data.id\n            }).then(res=>{\n                 if(res.data.code==1){\n                     this.$message({message:res.data.msg,type:'success'})\n                     this.init()\n                 }else{\n                     this.$message.error(res.data.mag)\n                 }\n            }).catch(err=>{\n                throw(err)\n            })\n            }\n      },\n      /* 搜索*/\n      search(){\n        \n            this.init()\n      },\n    \n      //操作分页组件\n      paging(e){\n        //把当前页赋值给page\n        this.page=e;\n        // 当前页发生改变时调取注册审核列表接口,重新渲染\n        this.init()\n      }\n    },\n    mounted(){\n      this.init()\n      this.$store.commit('increment')\n    }\n  }\n</script>\n\n<style lang=\"css\" scoped=\"scoped\">\n\n  .Head_portrait{\n    width: 50px;\n    height: 50px;\n    vertical-align: middle;\n    margin: 5px 0;\n    border-radius: 50%;\n  }\n  .search{\n    margin: 15px 0 15px 30px;\n  }\n   \n  .search>div{\n     width: 18%;\n     display: inline-block;\n     margin-right: 20px;\n  }\n  .search >>> .el-range-separator{\n     padding: 0\n  }\n\n\n  .Conduct>a{\n    margin-left: 20px;\n  }\n  .name{\n    width: 15%;\n  }\n  .table th{\n    height: 50px !important;\n    line-height: 50px !important;\n  }\n  .content{\n    width: 150px;\n    margin: 0 auto;\n  }\n  .Reason{\n    width: 50px;\n    display: inline-block;\n    white-space:nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    text-align: left;\n    float: left;\n    margin-left: 40px;\n  }\n  .rel{\n    position:relative;\n  }\n  .See{\n    float: left;\n  }\n  /*投诉人弹框*/\n  .ComplaintTable{\n    margin-bottom: 20px;\n  }\n  .ComplaintTable>tr>td{\n    width: 50% !important;\n    text-align: left;\n    padding: 0 10px;\n  }\n  .ComplaintTable>tr>td:last-child{\n    text-align: right;\n  }\n  /*投诉内容弹框*/\n  .ComplaintContent{\n    margin-top: 20px;\n    padding-bottom: 40px;\n  }\n  .reason{\n      min-height: 100px\n  }\n</style>\n<style >\n\n .el-date-table th{\n     background: none;\n  }\n  .search_btn{\n    border-radius: 0;\n    height: 38px;\n    width: 120px;\n  }\n  .input-with-select .el-input-group__prepend {\n    background-color: #fff;\n  }\n  .el-dialog__header{\n    padding-bottom: 20px;\n    border-bottom: 1px solid #E6E6EB;\n  }\n  .el-dialog__body{\n    padding-top: 0 !important;\n  }\n</style>\n"],"sourceRoot":""}]);

